import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import { ToolsProvider } from '../../providers/tools/tools';
import { LoginProvider } from '../../providers/login/login';
import { MarketProvider } from '../../providers/market/market';
import { Hist } from '../../models/hist';

@IonicPage()
@Component({
  selector: 'page-monitor-histori',
  templateUrl: 'monitor-histori.html',
})
export class MonitorHistoriPage {
  
  items: Hist[];  
  public clientId: string;
  public search: string='';

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public tool: ToolsProvider,
    public lgn: LoginProvider,
    public platform: Platform,
    public mkt: MarketProvider
  ) {
  }

  ionViewDidLoad() {
    this.platform.ready().then(()=>{
      this.checkLogin();
    });
  }

  checkLogin(){
    this.tool.getStg('login').then(
      (data)=>{
        if(!data || data.level !== '1'){
          this.navCtrl.setRoot('LoginPage');
        }else{
          this.clientId = data.clientId;
          this.tool.getStg('ip').then(
            (data)=>{
              if(data){
                this.lgn.logger(this.clientId,'Masuk halaman monitor-history', data.ip).subscribe();
              }
            }
          );
        }
      },
      err=>{
        console.log('Monitor History-> checkLogin', err);
      }
    );
  }

  getSearchData(){
    if(this.search.length > 3){
      this.getData();
    }
  }

  getData(){
    this.tool.showLoading();
    this.mkt.getData(this.search).subscribe(data => {
      let result:any;
      result = data;
      this.items = result.records;
      this.tool.stopLoading();
    },err=>{
      console.log('History-> getData()',err);
      this.tool.stopLoading();
    });
  }

}



